.resultsEntry{
    background-color: rgb(220,220,220);
    height: 100vh;
    overflow: auto;
}
.mainBack{
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('/static/chicago_gym.jpg');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 100vh;
    overflow: scroll;
}

.pageContainer{
    margin-left: 25px;
    margin-right: 25px;
    width: 100%;
    margin-bottom: 100px;
}

.subTitle{
    font-size: 24px;
    margin-top: 20px;
}

.choiceBox{
    width: 200px;
    background-color: #ffffff;
    border: #979797;
    cursor: pointer;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    margin-top: 10px;
}

.choiceBox:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

.activeBox{
    background-color: rgba(0,90,180,0.5);
    color: #ffffff;
}

.athleteBox{
    background-color: #ffffff;
    border: #979797;
    cursor: pointer;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    margin-top: 15px;
    font-size: 1.5em;
}

.athleteBox:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    background-color: rgb(230,230,230);
  }

  .completed{
    background-color: rgba(30, 197, 8, 0.5);
    color: #ffffff;
}

.scName{
    background-color: #ffffff;
    border: 2px solid rgb(200,200,200);
    padding: 15px;
    font-size: 1.5em;
    font-weight: bold;
}

.obstacleBox{
    min-width: 400px;
    background-color: #ffffff;
    border: #979797;
    cursor: pointer;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    margin-top: 10px;
}
.obstacleBoxTitle{
    font-size: 18px;
    border-bottom: 2px solid #bdbdbd;
    background-color: rgb(200,200,200);
    padding: 5px;
}
.obstacleBoxContent{
    font-size: 24px;
    padding: 10px;
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 3px;
    cursor: pointer;
}
.obstacleBoxContent:hover{
    background-color: rgb(230,230,230);
}

.skillInput{
    background-color: #ffffff;
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    padding: 5px;
    border: 2px solid rgb(200,200,200);
}
.submitBtn{
    margin-top: 25px;
    margin-bottom: 100px;
    color: #ffffff;
    background-color: rgba(0, 92,180,0.9);
    width: 100%;
    border: none;
    border-radius: 3px;
    padding: .5em;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.submitBtn:hover{
    background-color: rgba(255,255,255,0.8);
    color: rgb(0, 92,180);
    transition: 200ms;
}

.updateBtn{
    margin-top: 25px;
    margin-bottom: 100px;
    color: #ffffff;
    background-color: rgba(91, 183,91,0.9);
    width: 100%;
    border: none;
    border-radius: 3px;
    padding: .5em;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.updateBtn:hover{
    background-color: rgba(255,255,255,0.8);
    color: rgb(91, 183,91);
    transition: 200ms;
}